	module.exports = {"data":{"markdownRemark":{"html":"<p>2部屋を改装する、1つは会議室、もう1つは管理プロセス室です。ボランティアをした大学院生がいくつかいます。ほぼ一日。会議室613で壁を塗り、床を塗りました。私は明日 \"クリスタルペイント\"と呼ばれるものを磨くつもりです。過去の何も、それは最新の開発であるようです。この種のものは、海外の多くの場所で広く使われています。最初に試してみてください。良い場合は、別の場所で使用することを検討してください。</p>\n<p>部門は、ハードウェアを含む第一級レベルに自らを据えなければなりません。この点では、学校全体が現実を考慮する必要があります。学校を運営するには、ソフトウェアと知識とアイディアの創造、そして設備と設備の近代化であるハードウェアの2つの側面があります。私たちは現在、両方の分野で課題に直面しています。国際政治部門の管理方法国の近代化は、1つのユニットの近代化によって構成されています。</p>\n<p>そのような問題を抱えている人もいるし、時には政府がそれをするのを待たなければならないこともあり、それを行うイニシアチブはそれほど高くない。このような状況は、個人の文化の原因であり、社会的規制によって引き起こされる慣性でもあります。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0050.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0050.md","frontmatter":{"title":"3月5日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0050.md absPath of file >>> MarkdownRemark"}}

